<template>
  <div v-show="props.compType === 'date'">
    <!-- <el-form-item label="">
      <el-input class="input" v-model="props"></el-input>
    </el-form-item> -->
    <el-form-item label="ID">
      <el-tooltip class="item" effect="dark" content=",ID" placement="left">
        <el-input class="input" v-model="props.id" @change="handlerChangeId"></el-input>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" v-model="props.label"></el-input>
    </el-form-item>
    <!-- <el-form-item label="">
      <el-slider class="input" v-model="props.span" :max="24" :min="1" :marks="{12:''}"></el-slider>
    </el-form-item> -->
    <el-form-item label="">
      <el-input-number v-model="props.gutter"  :min="0"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.labelWidth"  :min="1" :max="200"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.showLabel"></el-switch>
    </el-form-item>
    <el-form-item  label="">
      <el-input class="input" v-model="props.placeholder"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.required"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-select class="input" v-model="props.type" @change="handlerFormatChange">
        <el-option v-for="item in dateTypeOptions" :key="item.value" text="item.label" :value="item.value"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" :value="props.format"/>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.clearable"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.readonly"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.disabled"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-date-picker class="input" v-model="props.value" type="date" placeholder=""/>
    </el-form-item>
  </div>
</template>
<script>
/**
 * input
 */
const dateType =[
    {
      label: '(date)',
      value: 'date'
    },
    {
      label: '(week)',
      value: 'week'
    },
    {
      label: '(month)',
      value: 'month'
    },
    {
      label: '(year)',
      value: 'year'
    },
    {
      label: '(datetime)',
      value: 'datetime'
    }
  ];
const dateTimeFormat = {
  date: 'yyyy-MM-dd',
  week: 'yyyy  WW ',
  month: 'yyyy-MM',
  year: 'yyyy',
  datetime: 'yyyy-MM-dd HH:mm:ss',
  daterange: 'yyyy-MM-dd',
  monthrange: 'yyyy-MM',
  datetimerange: 'yyyy-MM-dd HH:mm:ss'
}
export default {
  name:"inputConfig",
  props:['props','getFormId'],
  components: {
  },
  data(){
    return {
      dateTypeOptions:dateType
    }
  },
  methods:{
    handlerFormatChange(val){
      this.props.format = dateTimeFormat[val];
    },
    handlerChangeId(val){
      let idArray = this.getFormId(this.props._id);
      if(idArray.includes(val)){  //id
        this.$message.error('ID');
        this.props.id=this.props._id;
      }else{
        this.props._id=val;
      }
    }
  },
  mounted(){
  }
}
</script>
<style scoped>
.input{
  width:75%
}
</style>
