<template>
  <div v-show="props.compType === 'textarea'">
    <!-- <el-form-item label="">
      <el-input class="input" v-model="props"></el-input>
    </el-form-item> -->
    <el-form-item label="ID">
      <el-tooltip class="item" effect="dark" content=",ID" placement="left">
        <el-input class="input" v-model="props.id" @change="handlerChangeId"></el-input>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" v-model="props.label"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" v-model="props.placeholder" placeholder=""/>
    </el-form-item>
    <!-- <el-form-item label="">
      <el-slider class="input" v-model="props.span" :max="24" :min="1" :marks="{12:''}"></el-slider>
    </el-form-item> -->
    <el-form-item label="">
      <el-input-number v-model="props.gutter"  :min="0"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.labelWidth"  :min="1" :max="200"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.showLabel" @change="handlerChangeLabel"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.required"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.rows"  :min="0"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.maxlength" placeholder=""/>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props['show-word-limit']"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.disabled" @change="handlerChangeDisStatus"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.readonly" @change="handlerChangeReadStatus"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" v-model="props.value"></el-input>
    </el-form-item>
  </div>
</template>
<script>
/**
 * input
 */
export default {
  name:"inputConfig",
  props:['props','getFormId'],
  components: {
  },
  data(){
    return {
      val:123
    }
  },
  methods:{
    handlerChangeLabel(val){
      this.props.labelWidth = val?'80':'1'
    },
    handlerChangeDisStatus(val){
      this.props.readOnly = val?false:true
    },
    handlerChangeReadStatus(val){
      this.props.disabled = val?false:true
    },
    handlerChangeId(val){
      let idArray = this.getFormId(this.props._id);
      if(idArray.includes(val)){  //id
        this.$message.error('ID');
        this.props.id=this.props._id;
      }else{
        this.props._id=val;
      }
    }
  },
  mounted(){
  }
}
</script>
<style scoped>
.input{
  width:75%
}
</style>